import { defineComponent, ref, computed } from '@nuxtjs/composition-api';
export default defineComponent({
    props: {
        board: {
            type: Object,
            required: true
        }
    },
    emits: ['open-card', 'card-action', 'list-action'],
    setup(props, { emit }) {
        const isTaskOpened = ref(false);
        const currentList = ref(0);
        const lists = computed(() => props.board.lists);
        const cardsInList = (listName) => {
            return props.board.lists.filter((list) => list.name === listName);
        };
        const onCardAction = (event, card) => {
            emit('card-action', event, card);
        };
        const onListAction = (event, list) => {
            console.log('debug', 111, 'received event', list);
            emit('list-action', event, list);
        };
        return {
            lists,
            onListAction,
            onCardAction,
            currentList,
            cardsInList,
            isTaskOpened
        };
    }
});
